import * as React from 'react';
import { graphql, type PageProps } from 'gatsby';
import Layout from '@lekoarts/gatsby-theme-minimal-blog/src/components/layout';
import BlockTitle from '../components/block-title';
import ProjectCard from '../components/project-card';
import { styled } from 'styled-components';
import Seo from '@lekoarts/gatsby-theme-minimal-blog/src/components/seo';

const ProjectsList = styled.ul`
  margin: 0 auto;
  padding: 0;
`;

interface ProjectQueryResult {
  allProject: {
    totalCount: number;
    nodes: Array<{
      title: string;
      banner: string | null;
      logo: string | null;
      skills: string[] | null;
      collaboration: boolean | null;
      type: string | null;
      description: string[] | null;
      codeLink: string | null;
      order: number | null;
      demoLink: string | null;
    }>;
  };
}

const Projects = ({ data }: PageProps<ProjectQueryResult>) => {
  const projectsData = data?.allProject?.nodes;

  const blocksInfo = [
    {
      type: 'site',
      title: '',
      desc: ' 4  Next.js / Nuxt.js  SSR  RESTful API  GraphQL script(lazy-loading',
    },
    {
      type: 'report',
      title: '',
      desc: ' 4 ',
    },
    {
      type: 'side-project',
      title: 'Side Project',
      desc: ' Side project',
    },
  ];

  return (
    <Layout>
      {blocksInfo.map((block) => {
        return (
          <section key={block.type}>
            <BlockTitle title={block.title} description={block.desc} />
            <ProjectsList>
              {projectsData
                ?.filter((project) => project.type === block.type)
                .sort((a, b) => (a.order ?? 10) - (b.order ?? 10))
                .map((project, index) => (
                  <ProjectCard
                    key={project.title}
                    title={project.title}
                    description={project.description}
                    skills={project.skills}
                    banner={project.banner}
                    logo={project.logo}
                    codeLink={project.codeLink}
                    isImageRight={index % 2 !== 0}
                    demoLink={''}
                  />
                ))}
            </ProjectsList>
          </section>
        );
      })}
    </Layout>
  );
};

export const query = graphql`
  query allProjectQuery {
    allProject {
      nodes {
        title
        banner
        logo
        skills
        collaboration
        type
        description
        codeLink
        order
        demoLink
      }
    }
  }
`;

export const Head = () => (
  <Seo
    title='Projects | '
    description=''
    image='/default-project.webp'
    pathname='projects'
  />
);

export default Projects;
